import { getImagesByQuery } from './js/pixabay-api';
import {
  clearGallery,
  createGallery,
  hideLoader,
  hideLoadMoreButton,
  showLoader,
  showLoadMoreButton,
} from './js/render-functions';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const form = document.querySelector('.form');
const loadMoreBtn = document.querySelector('.load-more');
let inputValue = null;
let page = null;

form.addEventListener('submit', async event => {
  event.preventDefault();
  page = 1;
  inputValue = form.elements['search-text'].value.trim();
  clearGallery();

  if (inputValue === '') {
    iziToast.error({
      message: 'Please enter a search query!',
      position: 'topRight',
      timeout: 2000,
    });
    return;
  }

  showLoader();

  try {
    const response = await getImagesByQuery(inputValue, page);

    if (!response.data.hits.length) {
      iziToast.error({
        message:
          'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
        timeout: 2000,
      });
      hideLoadMoreButton();
      return;
    }

    createGallery(response);
    showLoadMoreButton();
  } catch (error) {
    iziToast.error({
      message: 'Error fetching data from Pixabay',
      position: 'topCenter',
      timeout: 3000,
    });
  } finally {
    hideLoader();
    form.reset();
  }
});

loadMoreBtn.addEventListener('click', onLoadMoreBtnClick);

async function onLoadMoreBtnClick() {
  page += 1;
  const galery_item = document.querySelector('.gallery-item');
  const galery_item_height = galery_item.getBoundingClientRect().height;

  const loaderWrapper = document.querySelector('.loader-wraper');
  const gallery = document.querySelector('.gallery');
  gallery.insertAdjacentElement('afterend', loaderWrapper);

  showLoader();

  try {
    const data = await getImagesByQuery(inputValue, page);
    createGallery(data);

    requestAnimationFrame(() => {
      const galery_item = document.querySelector('.gallery-item');
      const galery_item_height = galery_item.getBoundingClientRect().height;

      window.scrollBy({
        left: 0,
        top: galery_item_height * 2,
        behavior: 'smooth',
      });
    });
  } catch (error) {
    iziToast.error({
      message: 'Error fetching data from Pixabay',
      position: 'topCenter',
      timeout: 3000,
    });
  } finally {
    hideLoader();
  }
}
